import  { useLayoutEffect,  useState } from 'react'
import Tabs from './Tabs'
import "react-datepicker/dist/react-datepicker.css";
import Select from 'react-select';
import { DataFormateForSelect,  GetCountryList, GetStatementCycle } from '../../auth/core/_requests';
import { SearchAnItem } from '../../../../Util';

const initialValue = {
  country_id: "",
  statement_cycle_id: "",
  occupation_name: "",
  tin: "",
}

export function OtherInfo() {
  const [page, setPage] = useState("3")
  const [data, setData] = useState<any>(initialValue)
  const [err, seterr] = useState('')
  const [sucessMsg, setsucessMsg] = useState('')
  const [loading, setLoading] = useState(false)

  const [country, setcountry] = useState([])
  const [StatementList, setStatementList] = useState([])
  const [defaultCountry, setDefaultCountry] = useState<any>([])
  const [preview, setpreview] = useState<any>([])

  
  const handleSubmit = async(event:any)=>{

    const form = event.currentTarget;
    setLoading(false)
    event.preventDefault();
    if (form.checkValidity() === false) {
      event.stopPropagation();

      let fields = Object.keys(data);
      fields.map((collection:any,key:any)=>{
        let el:any = document.getElementsByName(collection)
        el[0].style.border = ""
        if (el[0].required && !el[0].value ) {
          el[0].style.border = "1px solid red"
        }else{
          el[0].parentElement.style.border = ""
          if (el[0].name === "country_id" && !data.country_id ) {
            el[0].parentElement.style.border = "1px solid red"
          }
        }
      })
      seterr("Please Select All requied Fields")
    }

    if (form.checkValidity() === true) {
      event.preventDefault();
      
      let pageInsert:any = {}
      pageInsert = {
        page_number: 3,
        type: "BO_OtherInfo",
        data,
        preview   
      }

      localStorage.setItem("Bo_create__OtherInfo" , JSON.stringify(pageInsert)  )
      setsucessMsg('Success!!!!')
      seterr("")

    }
  }

  const nidInput = async (e:any) => {
    setData( { ...data, [e.target.name]: e.target.value })
  }

  const nidInputCustom = async (field_name:string,e:any) => {
    setData( { ...data, [field_name]: e  })
  }


  const fetchData = async () => {
    try {
      let SessionData:any = localStorage.getItem("Bo_create__OtherInfo")
      let SessData = JSON.parse(SessionData)?.data
      let EditDatas:any = { }
      let fields = Object.keys(data);
      if (SessData) {
        setpreview(SessData.preview)

        fields.map((collection:any,key:any)=>{
          let el:any = document.getElementsByName(collection)
          EditDatas[el[0].name] = SessData[el[0]?.name]
        })
        setData(EditDatas)
      }

      const datasStatementCycle:any = await GetStatementCycle()
      if (datasStatementCycle.data.statusCode ===200) {
        setStatementList(datasStatementCycle.data.statements)
      }

      const datasCountry:any = await GetCountryList()
      if (datasCountry.data.statusCode ===200) {
        let datas =  await DataFormateForSelect(datasCountry.data.countries , "country_id" , "country_name")
        setcountry(datas)
        let selectedCountry = await SearchAnItem(datas , SessData?.country_id , "value" )
        setDefaultCountry(selectedCountry)
      }

  
    } catch (error) {
      console.error(error)
    }
  }

  useLayoutEffect(() => {

    fetchData()
  },[]);

  const saveDataPreview = (index:any , value:any , type:any ="")=>{

    if (type ==="select") {
      let val = value.value
      let el = value

      for (let i = 0; i < el.length; i++) {
        if (value.children[i].className == val) {
          setpreview({ ...preview , [index] : el[i].label })
        }
        
      }
    }else{
      setpreview({ ...preview , [index] : value})
    }
  }

  console.log("preview" , preview)

  return (
    <div className='row g-5 g-xxl-8'>

      <div className='BO_Create_header'>
        <h4>New BO Application </h4> 
      </div>

      <Tabs />


      <div className='card mb-5 mb-xl-10'>
      <div
        className='card-header border-0 cursor-pointer'
        role='button'
        data-bs-toggle='collapse'
        data-bs-target='#kt_account_profile_details'
        aria-expanded='true'
        aria-controls='kt_account_profile_details'
      >
        <div className='card-title m-0'>
          <h3 className='fw-bolder m-0'>Others Info</h3>
        </div>
      </div>

      <div id='kt_account_profile_details' className='collapse show'>
        <form onSubmit={handleSubmit} noValidate className='form'>
          <div className='card-body border-top p-9'>

            <div className='row mb-6'>
              <div className='col-lg-6 fv-row'>
                Citizen/Resident of <span style={{color:"red"}}>*</span>

                <Select
                  options={country}
                  name="country_id"
                  onChange={(e:any) => { nidInputCustom("country_id", e.value ); saveDataPreview("country_id",e.label); } }
                  className="custom_required"
                  placeholder={defaultCountry?.label}
                />
              </div>


              <div className='col-lg-6 fv-row'>
                Statement Cycle <span style={{color:"red"}}>*</span>
                <select className="form-select" aria-label="Select example" onChange={(e):any=> { saveDataPreview("statement_cycle_id",e.target , "select" ); nidInput(e); } } name='statement_cycle_id' required value={data?.statement_cycle_id}>
                  <option value="">Please Select</option>
                  {
                    StatementList.map((collection:any, key:number)=>
                    <option value={collection.statement_cycle_id} key={key} className={collection.statement_cycle_id}> {collection.statement_cycle} </option>
                    )
                  }
                  
                </select>
              </div>


              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Occupation 
                  
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Your Occupation"
                    onChange={(e):any=>{ saveDataPreview("occupation_name",e.target.value); nidInput(e); } }
                    name='occupation_name'
                    defaultValue={data?.occupation_name }
                  />
                  
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  TIN 
                  
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Your TIN Number"
                    onChange={(e):any=> { saveDataPreview("tin",e.target.value); nidInput(e); } }
                    name='tin'
                    defaultValue={data?.tin }
                  />
                  
                </div>
              </div>
            </div>


            <p style={{color:"red"}}>{err}</p>
            <p style={{color:"green"}}>{sucessMsg}</p>
          </div>

          <div className='card-footer d-flex justify-content-end py-6 px-9'>
            
            <button type='submit' className='btn btn-primary' disabled={loading}>
              {!loading && 'Save Changes'}
              {loading && (
                <span className='indicator-progress' style={{display: 'block'}}>
                  Please wait...{' '}
                  <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                </span>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
    </div>
  )
}
