import React, { useLayoutEffect, useRef, useState } from 'react'
import Tabs from './Tabs'
import { KTSVG, toAbsoluteUrl } from '../../../../_metronic/helpers';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const initialValue = {
  national_id: "",
  account_holder_name: "",
  father_or_husband_name: "",
  mother_name: "",
  dob: "",
  passport_number: "",
  place_of_passport: "",
  issue_date_of_passport: null,
  expiry_date_of_passport: null,
}

export function BasicInfo() {
  const [page, setPage] = useState("2")
  const [data, setData] = useState<any>(initialValue)
  const [err, seterr] = useState('')
  const [sucessMsg, setsucessMsg] = useState('')
  const [loading, setLoading] = useState(false)
  const [preview, setpreview] = useState<any>([])
  const stepperRef = useRef<HTMLDivElement | null>(null)

  const handleSubmit = async(event:any)=>{

    const form = event.currentTarget;
    setLoading(false)
    event.preventDefault();
    if (form.checkValidity() === false) {
      event.stopPropagation();

      let fields = Object.keys(data);
      fields.map((collection:any,key:any)=>{
        let el:any = document.getElementsByName(collection)
        if (el[0]?.required && !el[0]?.value ) {
          el[0].style.border = "1px solid red"
        }else{
          el[0].style.border = ""
        }
      })
      seterr("Please Select All requied Fields")
    }

    if (form.checkValidity() === true) {
      event.preventDefault();
      
      let pageInsert:any = {}
      pageInsert = {
        page_number: 2,
        type: "BO_basicInfo",
        data,
        preview    
      }

      localStorage.setItem("Bo_create__basicInfo" , JSON.stringify(pageInsert)  )
      setsucessMsg('Success!!!!')
      seterr("")

    }
  }

  const nidInput = async (e:any) => {

    let el:any = document.getElementsByName(e.target.name)    

    if (e.target.name == "account_holder_name" && e.target.value.length <3 ) {
      seterr("Account holder name must be at least 3 character")
      el[0].style.border = "1px solid red"
    }

    else if (e.target.name == "national_id") {
      console.log(e.target.value.length)

      seterr("")
      el[0].style.border = ""

      if ( (e.target.value.length == 10 ) ) {
        el[0].style.border = ""
        setData( { ...data, [e.target.name]: e.target.value })

      }else if(e.target.value.length == 13) {

        el[0].style.border = ""
        setData( { ...data, [e.target.name]: e.target.value })
        
      }else{
        seterr("NID should be 10 or 13 character")
        el[0].style.border = "1px solid red"
      }
    }

    else{
      el[0].style.border = ""
      setData( { ...data, [e.target.name]: e.target.value })
    }
    
  }

  const nidInputDate = async (field_name:string,e:any) => {
    setData( { ...data, [field_name]: e  })
  }


  const fetchData = async () => {
    try {
      let SessionData:any = localStorage.getItem("Bo_create__basicInfo")
      let SessData = JSON.parse(SessionData)?.data

      if (SessData?.preview) {
        let preview_data:any = SessData.preview
        setpreview(preview_data)
      }


      let EditDatas:any = { }
      let fields = Object.keys(data);
      if (SessData) {
        fields.map((collection:any,key:any)=>{
          let el:any = document.getElementsByName(collection)
          EditDatas[el[0].name] = SessData[el[0]?.name]
        })
        setData(EditDatas)
      }

      
  
    } catch (error) {
      console.error(error)
    }
  }

  useLayoutEffect(() => {
    fetchData()
  },[]);

  const saveDataPreview = (index:any , value:any)=>{
    setpreview({ ...preview , [index] : value})
  }

  return (
    <div className='row g-5 g-xxl-8'>

      <div className='BO_Create_header'>
        <h4>New BO Application </h4> 
      </div>

      <Tabs />


      <div className='card mb-5 mb-xl-10'>
      <div
        className='card-header border-0 cursor-pointer'
        role='button'
        data-bs-toggle='collapse'
        data-bs-target='#kt_account_profile_details'
        aria-expanded='true'
        aria-controls='kt_account_profile_details'
      >
        <div className='card-title m-0'>
          <h3 className='fw-bolder m-0'>Basic Info</h3>
        </div>
      </div>

      <div id='kt_account_profile_details' className='collapse show'>
        <form onSubmit={handleSubmit} noValidate className='form'>
          <div className='card-body border-top p-9'>

            <div className='row mb-6'>
              <div className='col-lg-4 fv-row'>
                
                <div className="mb-10">
                  Enter your NID no  <span style={{color:"red"}}>*</span>
                  <sup> 
                    <a href='#' className='btn btn-sm btn-primary' data-bs-toggle='modal' data-bs-target='#kt_modal_create_app' id='kt_toolbar_primary_button' >Help?</a> 
                  </sup>
                  
                  <input
                    type="number"
                    className="form-control"
                    placeholder="NID number should be at least 10 or 13 character"
                    onChange={(e):any=>{saveDataPreview("national_id",e.target.value); nidInput(e)} }
                    name='national_id'
                    required
                    defaultValue={data?.national_id}
                  />
                  
                </div>
              </div>
            </div>

            <div className='row mb-6'>
              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Your Name <span style={{color:"red"}}>*</span>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Name"
                    name='account_holder_name'
                    onChange={(e):any=>{ saveDataPreview("account_holder_name",e.target.value); nidInput(e); } }
                    required
                    defaultValue={data?.account_holder_name}
                  />
                  
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Father/Husband Name <span style={{color:"red"}}>*</span>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Father/Husband Name"
                    name='father_or_husband_name'
                    onChange={(e):any=> {saveDataPreview("father_or_husband_name",e.target.value); nidInput(e) } }
                    required
                    defaultValue={data?.father_or_husband_name}
                  />
                  
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Mother Name <span style={{color:"red"}}>*</span>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Mother Name"
                    name='mother_name'
                    onChange={(e):any=> {saveDataPreview("mother_name",e.target.value); nidInput(e); } }
                    required
                    defaultValue={data?.mother_name}
                  />
                  
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Date Of Birth <span style={{color:"red"}}>*</span>
                  <DatePicker className='form-control' selected={ data?.dob ?  new Date(data?.dob) : null } onChange={(date:any) => { saveDataPreview("dob", date ); nidInputDate("dob",date) } } required name="dob" />
                  
                </div>
              </div>
            </div>
            
            <h5 className='text-centerx'>Other Info</h5>
            

            <div className='row mb-6'>
              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Passport Number (optional)
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Passport Number"
                    name='passport_number'
                    onChange={(e):any=> { saveDataPreview("passport_number", e.target.value ); nidInput(e) }  }
                    defaultValue={data?.passport_number}
                    
                  />
                  
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Issue Place of Passport (optional)
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Issue Place of Passport"
                    name='place_of_passport'
                    onChange={(e):any=> { saveDataPreview("place_of_passport", e.target.value ); nidInput(e); } }
                    defaultValue={data?.place_of_passport}
                    
                  />
                  
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Issue Date of Passport (optional)
                  <DatePicker className='form-control' selected={ data?.issue_date_of_passport ?  new Date(data?.issue_date_of_passport) : null }  onChange={(date:any) => { saveDataPreview("issue_date_of_passport", date ); nidInputDate("issue_date_of_passport",date) } }  name="issue_date_of_passport" />
                  
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                
                <div className="mb-10">
                  Expiry Date of Passport (optional)
                  <DatePicker className='form-control' selected={ data?.expiry_date_of_passport ?  new Date(data?.expiry_date_of_passport) : null } onChange={(date:any) => { saveDataPreview("expiry_date_of_passport", date ); nidInputDate("expiry_date_of_passport",date) } }  name="expiry_date_of_passport" />
                  
                </div>
              </div>
            </div>



            <p style={{color:"red"}}>{err}</p>
            <p style={{color:"green"}}>{sucessMsg}</p>
          </div>

          <div className='card-footer d-flex justify-content-end py-6 px-9'>
            
            <button type='submit' className='btn btn-primary' disabled={loading}>
              {!loading && 'Save Changes'}
              {loading && (
                <span className='indicator-progress' style={{display: 'block'}}>
                  Please wait...{' '}
                  <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                </span>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>






      <div className='modal fade' id='kt_modal_create_app' aria-hidden='true'>
        <div className='modal-dialog modal-dialog-centered mw-900px'>
          <div className='modal-content'>
            <div className='modal-header'>
              <h2>Your NID number</h2>

              <div className='btn btn-sm btn-icon btn-active-color-primary' data-bs-dismiss='modal'>
                <KTSVG path='/media/icons/duotune/arrows/arr061.svg' className='svg-icon-1' />
              </div>
            </div>

            <div className='modal-body py-lg-10 px-lg-10'>
              <div
                ref={stepperRef}
                className='stepper stepper-pills stepper-column d-flex flex-column flex-xl-row flex-row-fluid'
                id='kt_modal_create_app_stepper'
              >
                <div className='d-flex justify-content-center justify-content-xl-start flex-row-auto w-100 w-xl-300px'>
                  <div className='stepper-nav ps-lg-10'>
                    <img src={toAbsoluteUrl('/stock-market/HelpNID.png')} />
                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
