import  { useLayoutEffect,  useRef,  useState } from 'react'
import Tabs from './Tabs'
import "react-datepicker/dist/react-datepicker.css";
import Select from 'react-select';
import { DataFormateForSelect,  GetCountryList, GetStatementCycle } from '../../auth/core/_requests';
import { SearchAnItem } from '../../../../Util';
import { KTSVG, toAbsoluteUrl } from '../../../../_metronic/helpers';

const initialValue = {
  parmanent_address: "",
  present_address: "",
  city: "",
  state: "",
  postal_code: "",
  country_address_id: "",
  contact_no : "",
  contact_no_2 : "",
  contact_fax : "",
  contact_email: ""
}

export function AddressDetails() {
  const [page, setPage] = useState("4")
  const [data, setData] = useState<any>(initialValue)
  const [err, seterr] = useState('')
  const [sucessMsg, setsucessMsg] = useState('')
  const [loading, setLoading] = useState(false)

  const stepperRef = useRef<HTMLDivElement | null>(null)

  const [country, setcountry] = useState([])
  const [checked, setchecked] = useState(false)
  const [defaultCountry, setDefaultCountry] = useState<any>([])

  const [preview, setpreview] = useState<any>([])

  
  const handleSubmit = async(event:any)=>{

    const form = event.currentTarget;
    setLoading(false)
    event.preventDefault();
    if (form.checkValidity() === false) {
      event.stopPropagation();

      let fields = Object.keys(data);
      fields.map((collection:any,key:any)=>{
        let el:any = document.getElementsByName(collection)

        el[0].style.border = ""
        if (el[0].required && !el[0].value ) {
          el[0].style.border = "1px solid red"
        }else{
          el[0].parentElement.style.border = ""
          if (el[0].name === "country_address_id" && !data.country_address_id ) {
            el[0].parentElement.style.border = "1px solid red"
          }
        }
      })
      seterr("Please Select All requied Fields")
    }

    if (form.checkValidity() === true) {
      event.preventDefault();
      
      let pageInsert:any = {}
      pageInsert = {
        page_number: 4,
        type: "BO_Address",
        data,
        preview    
      }

      localStorage.setItem("Bo_create__Address" , JSON.stringify(pageInsert)  )
      setsucessMsg('Success!!!!')
      seterr("")

    }
  }

  const nidInput = async (e:any) => {
    setData( { ...data, [e.target.name]: e.target.value })
  }

  const nidInputCustom = async (field_name:string,e:any) => {
    setData( { ...data, [field_name]: e  })
  }


  const fetchData = async () => {
    try {
      let SessionData:any = localStorage.getItem("Bo_create__Address")
      let SessData = JSON.parse(SessionData)?.data

      if (SessData?.preview) {
        let preview_data:any = SessData.preview
        setpreview(preview_data)
      }

      let EditDatas:any = { }
      let fields = Object.keys(data);
      if (SessData) {
        fields.map((collection:any,key:any)=>{
          let el:any = document.getElementsByName(collection)
          EditDatas[el[0].name] = SessData[el[0]?.name]
        })
        setData(EditDatas)
      }

      const datasCountry:any = await GetCountryList()
      if (datasCountry.data.statusCode ===200) {
        let datas =  await DataFormateForSelect(datasCountry.data.countries , "country_id" , "country_name")
        setcountry(datas)
        let selectedCountry = await SearchAnItem(datas , SessData?.country_address_id , "value" )
        setDefaultCountry(selectedCountry)
      }

  
    } catch (error) {
      console.error(error)
    }
  }

  const checkFunction =async (params:boolean) => {
    
    if (params) {
      setData({...data , ["present_address"] : data.parmanent_address })
    } 
    setchecked(!checked)
  }

  useLayoutEffect(() => {
    fetchData()
  },[]);

  const saveDataPreview = (index:any , value:any)=>{
    setpreview({ ...preview , [index] : value})
  }

  return (
    <div className='row g-5 g-xxl-8'>

      <div className='BO_Create_header'>
        <h4>New BO Application </h4> 
      </div>

      <Tabs />


      <div className='card mb-5 mb-xl-10'>
      <div
        className='card-header border-0 cursor-pointer'
        role='button'
        data-bs-toggle='collapse'
        data-bs-target='#kt_account_profile_details'
        aria-expanded='true'
        aria-controls='kt_account_profile_details'
      >
        <div className='card-title m-0'>
          <h3 className='fw-bolder m-0'>Address Details</h3>
        </div>
      </div>

      <div id='kt_account_profile_details' className='collapse show'>
        <form onSubmit={handleSubmit} noValidate className='form'>
          <div className='card-body border-top p-9'>

            <div className='row mb-6'>


              <div className='col-lg-6 fv-row'>
                <div className="mb-10">
                  Your Permanent Address <span style={{color:"red"}}>*</span>

                  <input
                    type="text"
                    className="form-control"
                    placeholder="Your Permanent Address"
                    onChange={(e):any=> { saveDataPreview("parmanent_address",e.target.value); nidInput(e); } }
                    name='parmanent_address'
                    required
                    defaultValue={data?.parmanent_address }
                  />
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                <div className="mb-10">
                  Your Present Address <span style={{color:"red"}}>*</span> 
                    <input className="form-check-input" type="checkbox" onClick={(e:any)=>checkFunction(!checked)} />
                    <label className="form-check-label">
                        Same as Permanent Address
                    </label>

                  <div className="mb-10">
                    <div className="form-check form-check-custom form-check-solid">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Your Present Address"
                          onChange={(e):any=> { saveDataPreview("present_address",e.target.value); nidInput(e); } }
                          name='present_address'
                          required
                          defaultValue={data?.present_address }
                        />
                    </div>
                  </div>
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                <div className="mb-10">
                  City <span style={{color:"red"}}>*</span>
                  
                  <input
                    type="text"
                    className="form-control"
                    placeholder="City"
                    onChange={(e):any=> { saveDataPreview("parmanent_address",e.target.value); nidInput(e); } }
                    name='city'
                    required
                    defaultValue={data?.city }
                  />
                </div>
              </div>

              <div className='col-lg-6 fv-row'>
                <div className="mb-10">
                  State/Division <span style={{color:"red"}}>*</span>
                  
                  <input
                    type="text"
                    className="form-control"
                    placeholder="State/Division"
                    onChange={(e):any=> { saveDataPreview("state",e.target.value); nidInput(e); } }
                    name='state'
                    required
                    defaultValue={data?.state }
                  />
                </div>
              </div>

 
              <div className='col-lg-6 fv-row'>
                Country <span style={{color:"red"}}>*</span>

                <Select
                  options={country}
                  name="country_address_id"
                  onChange={(e:any) => { saveDataPreview("country_address_id",e.label); nidInputCustom("country_address_id", e.value ); } }
                  className="custom_required"
                  placeholder={defaultCountry?.label}
                />
              </div>


              <div className='col-lg-6 fv-row'>
                <div className="mb-10">
                  Postal Code / ZipCode <span style={{color:"red"}}>*</span>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Postal Code / ZipCode"
                    onChange={(e):any=>{ saveDataPreview("postal_code",e.target.value); nidInput(e); }}
                    name='postal_code'
                    required
                    defaultValue={data?.postal_code }
                  />
                </div>
              </div>


              <div className='col-lg-6 fv-row'>
                <div className="mb-10">
                  Contact
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Contact"
                    onChange={(e):any=> { saveDataPreview("contact_no",e.target.value); nidInput(e); } }
                    name='contact_no'
                    required
                    defaultValue={data?.contact_no }
                  />
                  <br />
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Contact 2"
                    onChange={(e):any=>{ saveDataPreview("contact_no_2",e.target.value); nidInput(e); }}
                    name='contact_no_2'
                    
                    defaultValue={data?.contact_no_2 }
                  />
                  <br />

                  <input
                    type="text"
                    className="form-control"
                    placeholder="Fax"
                    onChange={(e):any=>{ saveDataPreview("contact_fax",e.target.value); nidInput(e); }}
                    name='contact_fax'
                    
                    defaultValue={data?.contact_fax }
                  />
                  <br />
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Email"
                    onChange={(e):any=>{ saveDataPreview("contact_email",e.target.value); nidInput(e); }}
                    name='contact_email'
                    required
                    defaultValue={data?.contact_email ? data?.contact_email : localStorage.getItem("email") }
                  />
                </div>
              </div>

            </div>


            <p style={{color:"red"}}>{err}</p>
            <p style={{color:"green"}}>{sucessMsg}</p>
          </div>

          <div className='card-footer d-flex justify-content-end py-6 px-9'>
            
            <button type='submit' className='btn btn-primary' disabled={loading}>
              {!loading && 'Save Changes'}
              {loading && (
                <span className='indicator-progress' style={{display: 'block'}}>
                  Please wait...{' '}
                  <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                </span>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>




    <div className='modal fade' id='kt_modal_create_app' aria-hidden='true'>
      <div className='modal-dialog modal-dialog-centered mw-900px'>
        <div className='modal-content'>
          <div className='modal-header'>
            <h2>Your NID number</h2>

            <div className='btn btn-sm btn-icon btn-active-color-primary' data-bs-dismiss='modal'>
              <KTSVG path='/media/icons/duotune/arrows/arr061.svg' className='svg-icon-1' />
            </div>
          </div>

          <div className='modal-body py-lg-10 px-lg-10'>
            <div
              ref={stepperRef}
              className='stepper stepper-pills stepper-column d-flex flex-column flex-xl-row flex-row-fluid'
              id='kt_modal_create_app_stepper'
            >
              <div className='d-flex justify-content-center justify-content-xl-start flex-row-auto w-100 w-xl-300px'>
                <div className='stepper-nav ps-lg-10'>
                  <img src={toAbsoluteUrl('/stock-market/HelpNID.png')} />
                </div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>


    </div>
  )
}
