import  { useLayoutEffect,  useState } from 'react'
import Tabs from './Tabs'
import "react-datepicker/dist/react-datepicker.css";
import Select from 'react-select';
import { DataFormateForSelect,  GetCountryList } from '../../auth/core/_requests';
import { SearchAnItem } from '../../../../Util';
import DatePicker from "react-datepicker";

const initialValue = {
  name: "",
  nid: "",
  father_or_husband_name: "",
  mother_name: "",
  dob: "",
  sex: "",
  percentage: "",
  relation_with: "",
  city: "",
  state: "",
  postal_code: "",
  nominee_country: "",
  phone:"",
  address:""
}

export function Nominees() {
  const [page, setPage] = useState("6")
  const [data, setData] = useState<any>(initialValue)
  const [err, seterr] = useState('')
  const [sucessMsg, setsucessMsg] = useState('')
  const [loading, setLoading] = useState(false)

  const [country, setcountry] = useState([])
  const [defaultCountry, setDefaultCountry] = useState<any>([])
  const [nominees, setNominees] = useState(false)
  const [preview, setpreview] = useState<any>([])

  
  const handleSubmit = async(event:any)=>{

    const form = event.currentTarget;
    setLoading(false)
    event.preventDefault();
    if (form.checkValidity() === false) {
      event.stopPropagation();

      let fields = Object.keys(data);
      fields.map((collection:any,key:any)=>{
        let el:any = document.getElementsByName(collection)
        el[0].style.border = ""
        if (el[0].required && !el[0].value ) {
          el[0].style.border = "1px solid red"
        }else{
          el[0].parentElement.style.border = ""
          if (el[0].name === "nominee_country" && !data.nominee_country ) {
            el[0].parentElement.style.border = "1px solid red"
          }
        }
      })
      seterr("Please Select All requied Fields")
    }

    if (form.checkValidity() === true) {
      event.preventDefault();

      let pageInsert:any = {}

      if (nominees) {

        pageInsert = {
          page_number: 6,
          type: "BO_Nominee",
          data,
          preview 
        }
        
      } else {

        pageInsert = {
          page_number: 6,
          type: "BO_Nominee",
          data: [],
          preview : []
        }
        
      }
      
      
      

      localStorage.setItem("Bo_create__Nominee" , JSON.stringify(pageInsert)  )
      setsucessMsg('Success!!!!')
      seterr("")

    }
  }

  const nidInput = async (e:any) => {
    setData( { ...data, [e.target.name]: e.target.value })
  }

  const nidInputCustom = async (field_name:string,e:any) => {
    setData( { ...data, [field_name]: e  })
  }

  const nidInputDate = async (field_name:string,e:any) => {
    setData( { ...data, [field_name]: e  })
  }


  const fetchData = async () => {
    try {
      let SessionData:any = localStorage.getItem("Bo_create__Nominee")
      let SessData = JSON.parse(SessionData)?.data

      if (SessData) {
        setData(SessData)
      }

      if (SessData?.preview) {
        let preview_data:any = SessData.preview
        setpreview(preview_data)
      }

      const datasCountry:any = await GetCountryList()
      if (datasCountry.data.statusCode ===200) {
        let datas =  await DataFormateForSelect(datasCountry.data.countries , "country_id" , "country_name")
        setcountry(datas)
        let selectedCountry = await SearchAnItem(datas , SessData?.nominee_country , "value" )
        setDefaultCountry(selectedCountry)
      }

  
    } catch (error) {
      console.error(error)
    }
  }

  useLayoutEffect(() => {
    fetchData()
  },[]);

  const saveDataPreview = (index:any , value:any)=>{
    setpreview({ ...preview , [index] : value})
  }

  console.log("data" , data)

  return (
    <div className='row g-5 g-xxl-8'>

      <div className='BO_Create_header'>
        <h4>New BO Application </h4> 
      </div>

      <Tabs />


      <div className='card mb-5 mb-xl-10'>
      <div
        className='card-header border-0 cursor-pointer'
        role='button'
        data-bs-toggle='collapse'
        data-bs-target='#kt_account_profile_details'
        aria-expanded='true'
        aria-controls='kt_account_profile_details'
      >
        <div className='card-title m-0'>
          <h3 className='fw-bolder m-0'>Nominees</h3>
        </div>
      </div>

      <div id='kt_account_profile_details' className='collapse show'>
        <form onSubmit={handleSubmit} noValidate className='form'>
          <div className='card-body border-top p-9'>

            <div className='row mb-6'>
              <div className='col-lg-12 fv-row'>
                Do you want to choos nominee(s) 

                <ul className='align-items-center mt-3 textDecorationNone'>
                    <li> 
                      <label className='form-check form-check-inline form-check-solid me-5'>
                        <input
                          className='form-check-input'
                          name='choose_nominee'
                          type='radio'
                          onClick={(e:any)=>setNominees(false)}
                        />
                        <span className='fw-bold ps-2 fs-6'>No</span>
                      </label>
                      <br />
                    </li>

                    <li> 
                      <label className='form-check form-check-inline form-check-solid me-5'>
                        <input
                          className='form-check-input'
                          name='choose_nominee'
                          type='radio'
                          onClick={(e:any)=>setNominees(true)}
                        />
                        <span className='fw-bold ps-2 fs-6'>Yes</span>
                      </label>
                      <br />
                    </li>
                </ul>
              </div>

              {
                nominees && 
              <>
              
              
              <h3> First Nominee Details </h3>
              <div className='row mb-6'>
              
                <div className='col-lg-4 fv-row'>
                  
                  <div className="mb-10">
                    Enter your NID no  <span style={{color:"red"}}>*</span>
                    <sup> 
                      <a href='#' className='btn btn-sm btn-primary' data-bs-toggle='modal' data-bs-target='#kt_modal_create_app' id='kt_toolbar_primary_button' >Help?</a> 
                    </sup>
                    
                    <input
                      type="number"
                      className="form-control"
                      placeholder="NID number"
                      onChange={(e):any=>{ saveDataPreview("nid",e.target.value); nidInput(e); }}
                      name='nid'
                      required
                      defaultValue={data?.nid}
                    />
                    
                  </div>
                </div>
              </div>

              <div className='row mb-6'>
                <div className='col-lg-6 fv-row'>
                  
                  <div className="mb-10">
                    Nominee's Name <span style={{color:"red"}}>*</span>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Name"
                      name='name'
                      onChange={(e):any=>{ saveDataPreview("name",e.target.value); nidInput(e); }}
                      required
                      defaultValue={data?.name}
                    />
                    
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  
                  <div className="mb-10">
                    Nominee's Father/Husband Name <span style={{color:"red"}}>*</span>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Father/Husband Name"
                      name='father_or_husband_name'
                      onChange={(e):any=>{ saveDataPreview("father_or_husband_name",e.target.value); nidInput(e); }}
                      required
                      defaultValue={data?.father_or_husband_name}
                    />
                    
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  
                  <div className="mb-10">
                    Nominee's Mother Name <span style={{color:"red"}}>*</span>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Mother Name"
                      name='mother_name'
                      onChange={(e):any=>{ saveDataPreview("mother_name",e.target.value); nidInput(e); }}
                      required
                      defaultValue={data?.mother_name}
                    />
                    
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  
                  <div className="mb-10">
                    Nominee's Date Of Birth <span style={{color:"red"}}>*</span>
                    <DatePicker className='form-control' selected={ data?.dob ?  new Date(data?.dob) : null } onChange={(date:any) => { saveDataPreview("dob",date); nidInputDate("dob",date) } } required name="dob" />
                    
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                    Sex <span style={{color:"red"}}>*</span>
                    <select className="form-select" aria-label="Select example" onChange={(e):any=> { saveDataPreview("sex",e.target.value); nidInput(e); } } name='sex' required value={data?.sex}>
                      <option value="">Please select</option>
                      <option value="male">Male</option>
                      <option value="female">Female</option>
                      <option value="other">Other</option>
                    </select>
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                  Percentage (%) <span style={{color:"red"}}>*</span>
                    <input
                        type="number"
                        max="100"
                        min="0"
                        className="form-control"
                        placeholder="Percentage"
                        name='percentage'
                        onChange={(e):any=>{ saveDataPreview("percentage",e.target.value); nidInput(e); }}
                        required
                        defaultValue={data?.percentage}
                      />
                  </div>
                  
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                    Relation with applicants <span style={{color:"red"}}>*</span>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Relation with applicants"
                      name='relation_with'
                      onChange={(e):any=>{ saveDataPreview("relation_with",e.target.value); nidInput(e); }}
                      required
                      defaultValue={data?.relation_with}
                    />
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                  Citizen/Resident of <span style={{color:"red"}}>*</span>

                    <Select
                      options={country}
                      name="nominee_country"
                      onChange={(e:any) => { saveDataPreview("custom_required",e.label); nidInputCustom("nominee_country", e.value ); } }
                      className="custom_required"
                      placeholder={defaultCountry?.label}
                    />
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                  Nominee's Address <span style={{color:"red"}}>*</span>

                    <input
                      type="text"
                      className="form-control"
                      placeholder="Address"
                      onChange={(e):any=>{ saveDataPreview("address",e.target.value); nidInput(e); }}
                      name='address'
                      required
                      defaultValue={data?.address }
                    />
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                  Nominee's City <span style={{color:"red"}}>*</span>
                    
                    <input
                      type="text"
                      className="form-control"
                      placeholder="City"
                      onChange={(e):any=>{ saveDataPreview("city",e.target.value); nidInput(e); }}
                      name='city'
                      required
                      defaultValue={data?.city }
                    />
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                  Nominee's State/Division <span style={{color:"red"}}>*</span>
                    
                    <input
                      type="text"
                      className="form-control"
                      placeholder="State/Division"
                      onChange={(e):any=>{ saveDataPreview("state",e.target.value); nidInput(e); }}
                      name='state'
                      required
                      defaultValue={data?.state }
                    />
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                    Postal Code / ZipCode <span style={{color:"red"}}>*</span>
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Postal Code / ZipCode"
                      onChange={(e):any=>{ saveDataPreview("postal_code",e.target.value); nidInput(e); }}
                      name='postal_code'
                      required
                      defaultValue={data?.postal_code }
                    />
                  </div>
                </div>

                <div className='col-lg-6 fv-row'>
                  <div className="mb-10">
                    Phone 
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Phome"
                      onChange={(e):any=>{ saveDataPreview("phone",e.target.value); nidInput(e); }}
                      name='phone'
                      defaultValue={data?.postal_code }
                    />
                  </div>
                </div>



              </div>

              </>

              }


            </div>


            <p style={{color:"red"}}>{err}</p>
            <p style={{color:"green"}}>{sucessMsg}</p>
          </div>

          <div className='card-footer d-flex justify-content-end py-6 px-9'>
            
            <button type='submit' className='btn btn-primary' disabled={loading}>
              {!loading && 'Save Changes'}
              {loading && (
                <span className='indicator-progress' style={{display: 'block'}}>
                  Please wait...{' '}
                  <span className='spinner-border spinner-border-sm align-middle ms-2'></span>
                </span>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
    </div>
  )
}
