/* eslint-disable react-hooks/rules-of-hooks */
import { Link } from "react-router-dom"

export default function Tabs() {

  let Name = ['DP & BO type' , 'Basic Info' , 'Other Info' , 'Address' , 'Bank Details' , 'Nominees' , 'Upload']
  let Routes = ['create' , 'basic' , 'other-info' , 'address' , 'bank-details' , 'nominees' , 'upload' ]
  let finishedPart = ['Bo_create__type' , 'Bo_create__basicInfo' , 'Bo_create__OtherInfo' , 'Bo_create__Address' , 'Bo_create__BankInfo' , 'Bo_create__Nominee' , 'Bo_create__upload']

  let part = finishedPart.map(( collection:any, key:number  ) =>{
    return localStorage.getItem(collection) ? key : null;
  })

  return (
    <div className="d-flex flex-row bd-highlight mb-3">
        {Name.map((record,key) => (
          part.includes(key)
            ? (
              <Link key={key}  to={"/bo-account/input/"+Routes[key]}>
                <div className= { window.location.pathname.replace("/bo-account/input/" , "") == Routes[key] ? "p-2 bd-highlight currentTab" : "p-2 bd-highlight activeTab"  } >
                  <h5>{ key + 1 }</h5>
                  <p>{record}</p>
                </div>
              </Link>
            )
            : (
              <Link key={key} to={"/bo-account/input/"+Routes[key]} >
                <div className= { window.location.pathname.replace("/bo-account/input/" , "") == Routes[key] ? "p-2 bd-highlight currentTab" : "p-2 bd-highlight"  } key={key}>
                  <h5>{ key + 1 }</h5>
                  <p>{record}</p>
                </div>
              </Link>
            )
        ))}
    </div>
  )
}
